
/*
    General
*/

@import url('https://fonts.googleapis.com/css?family=Raleway');
@import url('https://fonts.googleapis.com/css?family=Montserrat');

$dark: rgb(17, 17, 51);
$light: rgb(224, 224, 238);
$light-shadow: rgba(204, 204, 238, 0.5);
$dark-shadow: rgba(17, 17, 51, 0.9);

$dark-theme-black: rgb(3, 3, 13);
$dark-theme-gray: rgb(33, 33, 43);

body {
    background: gray;
    color: $light;
    font-family: 'Raleway', sans-serif;
}

a {
    text-decoration: none;
    color: $light;
}
a:hover {
    text-decoration: underline;
}

// remove weird button text border
button, button::-moz-focus-inner {
   outline:none;
   border: 0;
}

/* Hide HTML5 Up and Down arrows. */
input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
 
input[type="number"] {
    -moz-appearance: textfield;
}

#root {
    display: flex;
    justify-content: center;
}

/*
    Game
*/

.Game {
    width: 100%;
    box-shadow: 2px 5px 20px $dark-shadow;
    color: $light;
    background: $dark;
    text-align: center;
    padding: 20px;
    .Game-grid {
        margin-top: 10px;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        .Game-grid-row {
            display: flex;
        }
    }
    .Game-info {
        font-family: 'Montserrat', sans-serif;
    }
    .Game-menu {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        h1 {
            margin: 10px 0;
            color: $light;
        }
        .Game-input {
            font-family: inherit;
            border: none;
            box-shadow: 0 0 2px 2px $light-shadow;
            text-align: center;
            width: 4rem;
            font-size: 1rem;
            font-weight: bold;
            background: $light-shadow;
            color: $dark;
            padding: 5px 0;
            margin: 10px 10px 0 10px;
        }
        .Game-input:focus {
            background: $light;
            color: $dark;
        }
        .Game-button {
            font-family: inherit;
            position: relative;
            font-weight: bold;
            padding: 10px 20px;
            margin: 10px;
            color: $dark;
            background: $light-shadow;
            box-shadow: 0 1px 0 $dark-shadow;
            border-radius: 10px;
        }
        .Game-button:active, .toggle {
            box-shadow: 0 0 0;
            top: 1px;
            background: $light;
        }
    }
}


/*
    Popup
*/

.Popup {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .Popup-inner {
        background: $dark-shadow;
        color: $light;
        border: 1px solid $light-shadow;
        box-shadow: 0 0 5px 5px $dark-shadow;
        padding: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        p {
            padding: 0.2em 0.4em;
            font-size: 3em;
            box-sizing: border-box;
            text-align: center;
            margin: 0;
        }
        .Popup-score {
            p {
                font-size: 1.5em;
            }
            form {
                display: flex;
                align-items: flex-end;
                .Popup-name {
                    font-family: inherit;
                    background: $light-shadow;
                    color: $dark;
                    margin: 10px;
                    text-align: center;
                    border: none;
                    font-size: 1.3em;
                    padding: 5px 0;
                    width: 10em;
                }
                .Popup-name:focus {
                    background: $light;
                }
                .Popup-submit {
                    border: none;
                    font-family: inherit;
                    font-weight: bold;
                    padding: 10px 20px;
                    margin: 10px;
                    color: $dark;
                    background: $light-shadow;
                    box-shadow: 0 1px 0 $dark-shadow;
                    border-radius: 10px;
                }
                .Popup-submit:active {
                    background: $light;
                }
            }
        }
        .close {
            font-family: inherit;
            margin-top: 20px;
            border: none;
            background: $light-shadow;
            color: $dark;
            cursor: pointer;
            padding: 5px 15px;
            font-size: 1.3em;
        }
        .close:hover {
            background: $light;
        }
    }
}

/*
    Tile
*/

div[class*='Tile'] {
    text-align: center;
    width: 25px;
    height: 25px;
    box-sizing: border-box;
    box-shadow: 0 0 1px $dark;
    cursor: default;
    font-family: 'Montserrat', sans-serif;
    font-weight: 601;
    font-size: 1.3em;
    display: flex;
    justify-content: center;
    align-items: center;
    text-shadow: 0 0 1px $light-shadow;
}

.Tile-dark {
    background: $dark-theme-gray;
}

.Tile-light {
    background: $light;
}

.Flag {
    color: orange;
}

div[class*='Cover'] {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    box-shadow: 0 0 1px $light-shadow;
}

.Cover-dark {
    background: $dark-theme-black;
}

.Cover-light {
    background: $dark-shadow;
}

.Mine {
    color: gray;
    height: 100%;
}

